import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';

const styles = theme => ({
  root: {
    margin: '5px',
    padding: '15px 0 15px 0',
    borderBottom: '2px solid #eee',
  },
  imgRoot: {
    float: 'left',
    marginRight: '10px',
  },
  img: {
    height: '100px',
    width: '140px'
  },
  name: {
    fontWeight: '600'
  },
  description: {
    margin: '10px 0 10px 0',
    color: '#666',
    fontSize: '13px'
  },
  price: {
    fontWeight: '600'
  },
  endLine: {
    clear: 'both'
  }
});

class ProductItem extends React.Component {
  render() {
    return (
      <div className={this.props.classes.root}>
        <div className={this.props.classes.imgRoot}>
          <img src={this.props.productImg} className={this.props.classes.img} alt={this.props.productName} />
        </div>
        <div>
          <h2 className={this.props.classes.name}>
            {this.props.productName}
          </h2>
          <p className={this.props.classes.description}>
            {this.props.productDescription}
          </p>
          <p className={this.props.classes.price}>
            {this.props.productCurrencySign} {this.props.productPrice.toFixed(2)}
          </p>
        </div>
        <div className={this.props.classes.endLine}></div>
      </div>
    );
  }
}

ProductItem.propTypes = {
  classes: PropTypes.object.isRequired,
  productId: PropTypes.string.isRequired,
  productImg: PropTypes.string.isRequired,
  productName: PropTypes.string.isRequired,
  productDescription: PropTypes.string.isRequired,
  productCurrencySign: PropTypes.string.isRequired,
  productPrice: PropTypes.number.isRequired,
};

export default withStyles(styles)(ProductItem);


